(function ($) {
// toggle preview blocks visibility (show/hide)
    var toggle_visibility = function ($el, val, model, options) {
        $el.toggle(!!+val);
    };

    var photo_settings = function ($el, val, model, options) {
        var layout = model.get('layout');
        var $custom_width = $('.photo_width');
        var $top_margin = $('.photo_top_margin');
        var photo_sizes = {
            'a-full': '100%',
            /*'a-large': '600px',
             'a-medium': '300px',
             'a-thumbnail': '150px',
             'a-small': '50px',*/
        };

        toggle_visibility($el, val[0], model, options); // is block visible?
        var $wrapper = $el.find('.photo-wrapper');
        var $shape = $el.find('.photo-shape');
        var $photo = $el.find('.photo-container');

        $custom_width.hide();
        if ('round' == val[1]) {
            $shape.addClass('ats-round');
        }
        if ('square' == val[1]) {
            $shape.removeClass('ats-round');
        }

        if (layout == 'grid') {
            if (val[2] == 'a-full') {
                $el.css('margin', '0');
                $shape.css('width', photo_sizes[val[2]]);
                $photo.css('width', photo_sizes[val[2]]);
                $photo.css('padding-bottom', photo_sizes[val[2]]);
                $top_margin.hide();
            }
            if (val[2] == 'custom') {
                $el.css('margin-top', AwesomeUtil.calc_size(val[4]));
                $shape.css('width', AwesomeUtil.calc_size(val[3]));
                $custom_width.show();
                $top_margin.show();
            }
        }
        if (layout == 'table') {
            $el.css('width', AwesomeUtil.calc_size(val[3]));
            $shape.css('width', AwesomeUtil.calc_size(val[3]));
            $photo.css('width', AwesomeUtil.calc_size(val[3]));
            $photo.css('padding-bottom', AwesomeUtil.calc_size(val[3]));
            $custom_width.show();
        }
        if (layout == 'widget') {
            $shape.css('width', AwesomeUtil.calc_size(val[3]));
            $shape.css('min-width', AwesomeUtil.calc_size(val[3]));
            $photo.css('width', AwesomeUtil.calc_size(val[3]));
            $photo.css('padding-bottom', AwesomeUtil.calc_size(val[3]));
            $custom_width.show();
        }
    };

// handle preview block appearance on tooltip editor settings change
    var font_settings = function ($el, val, model, options) {
        var color_field = options.selector.replace_all('.employer_', '') + '_color';
        toggle_visibility($el, val[0], model, options); // is block visible?
        var $elc = $el.find('.team-field-content');
        //reset styles
        $elc.css('font-weight', 'normal');
        $elc.css('font-style', 'normal');
        $el.find('.font-style .btn').removeClass('active'); // reset font-style buttons

        $elc.css('font-size', val[1]); // font size
        $elc.css('color', val[2]); // text color

        if (!!+val[3]) {
            $el.find('[data-option-name="bold"]').addClass('active'); // set button "bold" to active state
            $elc.css('font-weight', 'bold');
        }
        if (!!+val[4]) {
            $el.find('[data-option-name="italic"]').addClass('active'); // set button "italic" to active state
            $elc.css('font-style', 'italic');
        }
        $elc.css('text-transform', val[5]); // is uppercase
        $elc.css('text-align', val[6]); //text align
        $el.css('margin-top', AwesomeUtil.calc_size(val[7])); //margin-top
    };

    var card_body_settings = function ($el, color, border, shadow) {
        $el.css('background-color', color);
        $el.css('border', '1px solid ' + border);
        if (shadow !== '') {
            $el.css('-webkit-box-shadow', '0 0 5px 0 ' + shadow);
            $el.css('-moz-box-shadow', '0 0 5px 0 ' + shadow);
            $el.css('box-shadow', '0 0 5px 0 ' + shadow);
        }
    };

// Backbone View for team preview
    TeamPreview = Backbone.View.extend({
        initialize: function (options) {
            _.bindAll(this, 'before_render', 'render', 'after_render'); // create callbacks for 'before_render' and 'after_render' pseudo events
            var _this = this;
            this.render = _.wrap(this.render, function (render) {
                _this.before_render();
                render();
                _this.after_render();
                return _this;
            });
        },

        // data bindings
        bindings: function () {
            var result = {};
            var layout = model.get('layout');
            var model_field = 'styles.' + layout + '.';

            // team base gaps and background color
            result['#team-preview .ats-layout'] = {
                observe: model_field + 'hover',
                update: function ($el, val, model, options) {
                    if (val == 'off') {
                        $el.attr('class', 'ats-layout-' + model.get('layout') + ' ats-layout ');
                    } else {
                        $el.attr('class', 'ats-layout-' + model.get('layout') + ' ats-layout ' + 'ats-effect-' + val);
                    }
                }
            };


            // team base gaps and background color
            result['#team-preview ul.employers-box'] = {
                observe: [
                    model_field + 'gaps',
                    model_field + 'base_color',
                    model_field + 'align'
                ],
                update: function ($el, val, model, options) {
                    if (model.get('layout') == 'grid') {
                        $el.css('background-color', val[1]);
                        $el.css('text-align', val[2]);
                    }
                }
            };


            // team card base color, border color, shadow color and gaps
            result['#team-preview ul.employers-box > li > div'] = {
                observe: [
                    model_field + 'card_base_color',
                    model_field + 'card_border_color',
                    model_field + 'card_shadow_color',
                    model_field + 'card_gaps',
                    model_field + 'body_visible'
                ],
                update: function ($el, val, model, options) {
                    $el.parent('li').css('padding', '0');
                    //$el.parent('li').css('padding', '0');
                    if (model.get('layout') == 'grid') {
                        if (!!+val[4]) {
                            card_body_settings($el, val[0], val[1], val[2]); // set card body bg, border and shadow
                        } else {
                            $el.css('background-color', 'transparent');
                            $el.css('border', 'none');
                            $el.css('-webkit-box-shadow', 'none');
                            $el.css('-moz-box-shadow', 'none');
                            $el.css('box-shadow', 'none');
                        }
                        if (model.get('styles.grid.photo_size') == 'a-full') {
                            $el.css('padding', '0');
                        }
                    } else {
                        $el.css('border-top-color', val[1]);
                        $el.css('border-bottom-color', val[1]);
                    }
                }
            };

            // divider
            result['.employer_divider'] = {
                observe: [
                    model_field + 'divider_visible',
                    model_field + 'divider_color',
                    model_field + 'divider_width',
                    model_field + 'divider_top_margin',
                ],
                update: function ($el, val, model, options) {
                    if (model.get('layout') == 'grid') {
                        toggle_visibility($el, val[0], model, options); // divider visible
                        $el.find('span').css('background-color', val[1]); // divider color
                        $el.find('span').css('width', AwesomeUtil.calc_size(val[2])); // divider width
                        $el.css('margin-top', AwesomeUtil.calc_size(val[3])); // divider top margin
                    }
                }
            };

            // team layout slider
            result['.card_margin'] = {
                observe: model_field + 'slider',
                update: function ($el, val, model, options) {
                    $el.hide();
                    if (val === 0 && model.get('layout') == 'grid') {
                        $el.show();
                    }
                }
            };

            // team layout align
            result['.team-align'] = {
                observe: model_field + 'align',
                update: function ($el, val, model, options) {
                    $el.find('.btn').removeClass('active');
                    $el.find('.align-' + val).addClass('active');
                }
            };

            // employer photo block settings
            result['.employer_photo'] = {
                observe: [
                    model_field + 'photo_visible',
                    model_field + 'photo_shape',
                    model_field + 'photo_size',
                    model_field + 'photo_width',
                    model_field + 'photo_top_margin'
                ],
                update: photo_settings
            };

            // employer name block bindings
            result['.employer_name'] = {
                observe: [
                    model_field + 'name_visible',
                    model_field + 'name_font_size',
                    model_field + 'name_color',
                    model_field + 'name_bold',
                    model_field + 'name_italic',
                    model_field + 'name_text_transform',
                    model_field + 'name_align',
                    model_field + 'name_top_margin'
                ],
                update: font_settings
            };

            // employer position block bindings
            result['.employer_position'] = {
                observe: [
                    model_field + 'position_visible',
                    model_field + 'position_font_size',
                    model_field + 'position_color',
                    model_field + 'position_bold',
                    model_field + 'position_italic',
                    model_field + 'position_text_transform',
                    model_field + 'position_align',
                    model_field + 'position_top_margin'
                ],
                update: font_settings
            };

            // employer short bio block bindings
            result['.employer_short_bio'] = {
                observe: [
                    model_field + 'short_bio_visible',
                    model_field + 'short_bio_font_size',
                    model_field + 'short_bio_color',
                    model_field + 'short_bio_bold',
                    model_field + 'short_bio_italic',
                    model_field + 'short_bio_text_transform',
                    model_field + 'short_bio_align',
                    model_field + 'short_bio_top_margin'
                ],
                update: font_settings
            };

            // employer phone block settings
            result['.employer_phone'] = {
                observe: [
                    model_field + 'phone_visible',
                    model_field + 'contacts_font_size',
                    model_field + 'contacts_color',
                    model_field + 'contacts_bold',
                    model_field + 'contacts_italic',
                    model_field + 'contacts_text_transform',
                    model_field + 'contacts_align',
                    model_field + 'contacts_top_margin'
                ],
                update: font_settings
            };
            // employer phone block settings
            result['.employer_email'] = {
                observe: [
                    model_field + 'email_visible',
                    model_field + 'contacts_font_size',
                    model_field + 'contacts_color',
                    model_field + 'contacts_bold',
                    model_field + 'contacts_italic',
                    model_field + 'contacts_text_transform',
                    model_field + 'contacts_align',
                    model_field + 'contacts_top_margin'
                ],
                update: font_settings
            };
            // employer social block settings
            result['.employer_skype'] = {
                observe: [
                    model_field + 'skype_visible',
                    model_field + 'contacts_font_size',
                    model_field + 'contacts_color',
                    model_field + 'contacts_bold',
                    model_field + 'contacts_italic',
                    model_field + 'contacts_text_transform',
                    model_field + 'contacts_align',
                    model_field + 'contacts_top_margin'
                ],
                update: font_settings
            };
            // employer link block settings
            result['.employer_link'] = {
                observe: [
                    model_field + 'link_visible',
                    model_field + 'contacts_font_size',
                    model_field + 'contacts_color',
                    model_field + 'contacts_bold',
                    model_field + 'contacts_italic',
                    model_field + 'contacts_text_transform',
                    model_field + 'contacts_align',
                    model_field + 'contacts_top_margin'
                ],
                update: font_settings
            };
            // employer link block settings
            result['.employer_location'] = {
                observe: [
                    model_field + 'location_visible',
                    model_field + 'contacts_font_size',
                    model_field + 'contacts_color',
                    model_field + 'contacts_bold',
                    model_field + 'contacts_italic',
                    model_field + 'contacts_text_transform',
                    model_field + 'contacts_align',
                    model_field + 'contacts_top_margin'
                ],
                update: font_settings
            };
            // employer social block settings
            result['.employer_social'] = {
                observe: [
                    model_field + 'social_visible',
                    model_field + 'social_color',
                    model_field + 'social_size',
                    model_field + 'social_top_margin',
                    model_field + 'social_align'
                ],
                update: function ($el, val, model, options) {
                    toggle_visibility($el, val[0], model, options);
                    $el.css('color', val[1]);
                    $el.css('font-size', val[2]);
                    $el.css('margin-top', AwesomeUtil.calc_size(val[3]));
                    $el.css('text-align', val[4]);

                    $el.css('font-size', 'initial');
                    var width = $el.outerWidth();
                    var icon_max_size = (width / 6) - 5;
                    $el.css('font-size', AwesomeUtil.clean_number(val[2]) > icon_max_size ? icon_max_size : val[2]);
                }
            };

            // skills block settings
            result['.employer_skills'] = {
                observe: [
                    model_field + 'skills_visible',
                    model_field + 'skills_font_size',
                    model_field + 'skills_text_color',
                    model_field + 'skills_bold',
                    model_field + 'skills_italic',
                    model_field + 'skills_text_transform',
                    model_field + 'skills_align',
                    model_field + 'skills_top_margin'
                ],
                update: font_settings
            }
            result['.employer_skills ul'] = {
                observe: [
                    model_field + 'skills_size',
                    model_field + 'skills_bar_color',
                    model_field + 'skills_type',
                ],
                update: function ($el, val, model, options) {
                    // bar size & color
                    $el.find('span.value').css('height', val[0]);
                    $el.find('svg').css('width', val[0]);
                    $el.find('svg').css('height', val[0]);
                    $el.find('span.value').css('background-color', val[1]);
                    $el.find('svg').css('color', val[1]);
                    $el.find('.skill-value').css('width', '100%');
                    $el.find('.skill-value').attr('class', 'skill-value skill-value-' + val[2]);
                    if (val[2] == 'star') {
                        var raw = val[0].replace(/\D+/g, '');
                        $el.find('.skill-value').css('width', AwesomeUtil.calc_size(raw * 5 + ''));
                        $el.find('.star-fill').css('width', AwesomeUtil.calc_size(raw * 4 + ''));
                    }
                    // type
                    $el.find('.skill-value > div').hide();
                    $el.find('.skill-value .skills-' + val[2]).show();

                    $el.find('.skill-value-star').css('max-width', '100%');
                    var fill_max_width = $el.find('.star-background').outerWidth();
                    fill_max_width = Math.round(fill_max_width / 10) * 10;

                    $el.find('.skill-value-star').css('max-width', fill_max_width);
                    $el.find('.star-fill').css('max-width', fill_max_width * 0.8);

                    var icon_max_width = $el.find('.star-background > *').outerWidth();

                    $el.find('.star-background > *').each(function (index, item) {
                        $(item).css('max-height', icon_max_width);
                    });
                    $el.find('.star-fill > *').each(function (index, item) {
                        $(item).css('max-width', icon_max_width);
                        $(item).css('max-height', icon_max_width);
                    });
                }
            };
            return result;
        },
        // before render callback
        /* jshint multistr:true */
        before_render: function () {
            var button = '<button data-action="call-tooltip">\
            <svg class="icon" viewBox="0 0 28 28">\
            <use xlink:href="#icon-pen"></use>\
            </svg>\
            </button>';
            this.$el.find('#team-preview .grid-container').append(button);

        },
        render: function () {
            preview.insert_preview_html();
            this.stickit();
            return this;
        },
        // after render callback
        after_render: function () {
            /*var sizes = ['full', 'large', 'medium', 'thumbnail'];
             var a_ajax = new AwesomeAjax();
             var $parent = jQuery('#a-slide-panel .employer_photo');
             var id = $parent.parents('li').attr('employer-id');

             jQuery.each(sizes, function (index, value) {
             //a_ajax.get_team_preview_photo($parent, id, value);
             })*/
        },
        close: function () {
            this.unstickit();
        }
    });
})(jQuery);