/**
 * Employer-related actions
 */
(function ($) {
    'use strict';

    $(function () {
        var model_employer = 'employer';
        var a_ajax = new AwesomeAjax('ats');
        var ats_ajax = new ATS_Ajax();
        var ats_animation = new ATS_Animation();

        // Add New Employer Button
        $('[data-action="add-employer"]').on('click', function () {
            var args = {
                id: 'ats',
                item_id: '#',
                parent: '#wpcontent',
                html: '#employer-add-wrap',
                model_name: model_employer,
                direction: 'right',
                width: '67%',
                load_model: employer_load_model,
                callback_open: edit_employer_open_callback,
                callback_close: edit_employer_close_callback
            };
            window.panel = new AwesomePanel(args);
            panel.init();
        });

        function employer_load_model(id, model_name) {
            return ats_ajax.load_model(id, model_name).then(function () { // wait until backbone model is loaded
                return ats_ajax.init_departments().then(function () { // wait until all possible departments is loaded
                    var dfd = $.Deferred();
                    var dfd1 = $.Deferred();

                    window.selectize_instance = $('[name="employer_department"]', ats_panel).selectize({
                        plugins: ['restore_on_backspace', 'remove_button'],
                        delimiter: ',',
                        addPrecedence: false,
                        options: ats_employers_taxonomy_terms || [],
                        items: model.get('department') || [],
                        valueField: 'name',
                        labelField: 'name',
                        searchField: 'name',
                        preload: true,

                        onInitialize: function () {
                            dfd1.resolve('selectize init'); // selectize is initialized => resolve promise
                        },

                        create: function (input) {
                            return {
                                value: input,
                                name: input
                            }
                        },

                        render: {
                            option: function (item, escape) {
                                return '<div>' + escape(item.name) + '</div>';
                            }
                        }
                    });

                    ats_ajax.fill_employer_panel_text(ats_panel, 'employer_panel_text', model.get('custom_fields.panel_text'));

                    $.when(dfd1, employer_panel_text_dfd).done(function (v1, v2) {
                        dfd.resolve('both init');
                    });

                    return dfd;
                });
            })
        }

        function edit_employer_open_callback() {
            $('#employer-form-foto-remove', ats_panel).hide(); // show "remove" button

            window.preview = new AwesomeEmployer();
            preview.init();
        }

        function edit_employer_close_callback() {
            if (
                typeof (selectize_instance) != 'undefined' &&
                typeof (selectize_instance[0]) != 'undefined' &&
                typeof (selectize_instance[0].selectize) != 'undefined'
            ){
                selectize_instance[0].selectize.destroy();
            }
            if(typeof preview != 'undefined'){
                preview.destroy();
            }
            if(typeof tinyMCEPreInit !== 'indefined'){
                tinyMCEPreInit.mceInit = {}
                tinyMCEPreInit.qtInit = {}
            }
            $('div.employer_panel_text').html('');
        }

        // Edit Employer
        $('[data-action="edit-employer"]').on('click', function (e) {
            var id = parseInt($(this).attr('employer-id'));

            // if we hit on employer but not Delete button
            if (
                $(e.target).parents('button').attr('class') !== 'delete-employer' &&
                $(e.target).attr('class') !== 'delete-employer'
            ) {

                var args = {
                    id: 'ats',
                    item_id: id,
                    parent: '#wpcontent',
                    html: '#employer-edit-wrap',
                    model_name: model_employer,
                    direction: 'right',
                    width: '67%',
                    load_model: employer_load_model,
                    callback_open: edit_employer_open_callback,
                    callback_close: edit_employer_close_callback
                };

                window.panel = new AwesomePanel(args);
                panel.init();

            }
        });

        function set_employer_photo_callback(img) {
            $('#employers-list li[employer-id="' + model.get('id') + '"] .foto').html(img);
        }

        function update_employer_card() {
            var post_id = model.get('id');
            ats_ajax.set_employer_photo(post_id, 'a-small', set_employer_photo_callback);
            $('#employers-list li[employer-id="' + post_id + '"] span.name').html(model.escape('name'));
            $('#employers-list li[employer-id="' + post_id + '"] span.position').html(model.escape('custom_fields.position'));
        }

        // Bind AJAX call on Form Submit Button
        $('#employer-edit-wrap .save-panel, #employer-add-wrap .save-panel').on('click', function () {
            var form = $('[data-type="form"]', ats_panel);
            var model = $(form).attr('data-model');
            var $alert = form.find('[data-type="employer-alert"]');
            $alert.attr('class', 'alert').hide();

            window.panel.hide_content();
            window.panel.animation_start(); // show animation

            a_ajax.ajax({
                action: 'ats_update_or_new_' + model,
                data: {
                    data: window.model.toJSON()
                },
                success_handler: function (data) {
                    window.model.set('id', data);
                    update_employer_card();
                    window.panel.show_content();
                    window.panel.animation_end();
                },
                success_error_handler: function (errors) {
                    var html = '';
                    $.each(errors, function (index, value) {
                        html += '<p>';
                        html += value.message;
                        html += '</p>';
                    });
                    $alert.addClass('alert-warning').show().html(html);
                    window.panel.show_content();
                    window.panel.animation_end();
                },
                error_handler: function () {
                    $alert.addClass('alert-warning').show().html(data);
                    window.panel.show_content();
                    window.panel.animation_end();
                }
            });
        });

        // Delete Employer
        $('button.delete-employer').on('click', function () {
            var $parent = $(this).parents('li');
            var id = parseInt($parent.attr('employer-id'));
            var model_name = model_employer;

            if (confirm('You really want to delete employer?')) {
                a_ajax.delete_model(id, model_name).then(function () {
                    ats_animation.delete_model($parent, model_name);
                });
            }
        });

    });
})(jQuery);