/**
 * Employer preview
 */

var file_frame;

var AwesomeEmployer = function (){
    'use strict';

    var ats_animation = new ATS_Animation();
    var $ = jQuery; // enable jQuery
    var self = this;

    // two way data bindings for edit team form
    self.bind_form = function(){
        var dfd = $.Deferred();

        // Upload employer avatar via built-in Wordpress media library
        $(document).on('click', '[data-action="upload_photo"]', function(e){
            e.preventDefault();

            var json;
            var url;

            if(undefined !== file_frame){
                file_frame.open();
                return;
            }

            file_frame = wp.media.frames.file_frame = wp.media({
                frame: 'post',
                state: 'insert',
                multiple: false
            });

            // handle image insert on media window submit
            file_frame.on('insert', function(){
                var $form = $(this).parents('.foto-upload');
                json = file_frame.state().get('selection').first().toJSON();
                if(0 > $.trim(json.url.length)){
                    return;
                }
                url = json.sizes['a-thumbnail'] ? json.sizes['a-thumbnail'].url : json.url;
                var $img = $('<img src="' + url + '"/>');

                $form.find('[name="employer_thumbnail_id"]').val(json.id); // set thumbnail ID to hidden field
                model.set('custom_fields.thumbnail_id', json.id);
                $('.foto-wrapper', ats_panel).html('').append($img); // show image
                $('#employer-form-foto-remove', ats_panel).show(); // show "remove" button
                file_frame.close();
            });

            file_frame.open();
            return false;
        });

        // Remove employer avatar
        $(document).on('click', '[data-action="remove_photo"]', function(e){
            e.preventDefault();
            $('[name="employer_thumbnail_id"]', ats_panel).val(''); // remove thumbnail ID from hidden field
            $('#employer-form-foto', ats_panel).html(''); // remove thumbnail image
            window.model.set('custom_fields.thumbnail_id', '');
            $(this).hide();
        });

        dfd.resolve();
        return dfd;
    };

    self.update_skill_order = function(){
        var skills = model.get('custom_fields.skills');
        var order = _.map($('.skills-list li'), function (el) {
            return parseInt(
                $(el).attr('id')
                    .replace_all('skill-', '')
            );
        });

        model.set('custom_fields.skills_order', order);
    };

    self.make_skill = function (form) {
        var $name = $('[name="employer_skill_name"]', form);
        var value = $('.skill-value-percent [name="employer_skill_value"]', form).val();
        var $alert = $('[data-type="skill-alert"]', form);

        $alert.attr('class', 'alert').hide();

        // validation
        if($name.val() == ''){
            $alert.addClass('alert-warning').show().html('Skill name can\'t be empty.');
            return false;
        }

        var skill = {
            name: $name.val(),
            //type: type,
            value: value
        };

        return skill;
    }

    self.bind_skills = function () {
        $('.panel-skills').on('click', '[data-action="add-skill"]', function () {
            // elements
            var form = $('[data-form="add-skill"]', ats_panel);
            var skill = self.make_skill(form);
            if(skill){
                var skills = model.get('custom_fields.skills') || [];
                model.push('custom_fields.skills_order', skills.length);

                $('[name="employer_skill_name"]', form).val('');
                model.push('custom_fields.skills', skill);
            }
        });

        $('.skills-list').on('click', '[data-action="edit-skill"]', function () {
            var $skill = $(this).parents('li');
            var index = $skill.attr('id').replace_all('skill-', '');

            employer_form_view.edit_skill(index);
        });

        $('.skills-list').on('click', '[data-action="cancel-skill-edit"]', function () {
            $('.skills-list .skill-inline-form', ats_panel).remove();
            $('.skills-list li.skill .skill-box').show();
        });

        $('.skills-list').on('click', '[data-action="save-skill"]', function(){
            var form = $(this).parents('[data-form="edit-skill"]');
            var index = $(this).parents('li.skill').attr('id').replace_all('skill-', '');
            var skills = model.get('custom_fields.skills');
            var skill = self.make_skill(form);
            var $spinner = $(this).find('.a-spinner');
            var $text = $(this).find('.text');

            if(skill){
                $text.css('visibility', 'hidden');
                $spinner.show();
                skills[index] = skill;
                model.set('custom_fields.skills', skills);
                setTimeout(function () {
                    $spinner.hide();
                    $text.css('visibility', 'visible');
                }, 900);
            }
        });

        $('.skills-list').on('click', '[data-action="delete-skill"]', function () {
            var $skill = $(this).parents('li');
            var index = $skill.attr('id').replace_all('skill-', '');
            var skills_order = model.get('custom_fields.skills_order');

            ats_animation.delete($skill).done(function () {
                var new_order = _.filter(skills_order, function (item) {
                    return item != index;
                });
                new_order = _.map(new_order, function (item) {
                    item = parseInt(item);
                    return item > index ? item - 1 : item;
                });
                model.set('custom_fields.skills_order', new_order);
                model.unset_array('custom_fields.skills', index);
            });
        });

        $('.panel-skills').on('input', 'input[name="employer_skill_value"]', function (e) {
            var value = $(this).val();

            value = value.replace(/\D+/g, '');
            value = parseInt(value) > 100 ? 100 : value;

            $(this).val(value);
        });

        $('.skills-list').sortable({
            axis: 'y',
            containment: "parent",
            forceHelperSize: true,
            forcePlaceholderSize: true,
            helper: "clone",
            update: function(event, ui){
                self.update_skill_order();
            }
        });
    }

    // destroy preview
    self.destroy = function(){
        $('.panel-skills').off('click', '[data-action="add-skill"]');
        $('.skills-list').off('click', '[data-action="save-skill"]');
        $('.skills-list').off('click', '[data-action="edit-skill"]');
        $('.skills-list').off('click', '[data-action="delete-skill"]');
        $('.skills-list').off('click', '[data-action="cancel-skill-edit"]');

    };

    // init preview
    self.init = function(){
        self.bind_form();
        self.bind_skills();
        var form = new AwesomeForm();
        form.init(model.attributes);

        window.employer_form_view = new EmployerForm({
            model: window.model
        });
        employer_form_view.setElement($('#employer-form', ats_panel));
        employer_form_view.render();
    };
};