/**
 * Team-related actions
 */
(function( $ ) {
    'use strict';

    $(function() {
        var model_team = 'team';
        var drag = new AwesomeDrag();
        var a_ajax = new AwesomeAjax('ats');
        var ats_ajax = new ATS_Ajax();
        var ats_animation = new ATS_Animation();

        // Add New Team Button
        $('[data-action="add-team"]').on('click', function(){
            var args = {
                id: 'ats',
                item_id: '#',
                parent: '#wpcontent',
                html: '#team-add-wrap',
                model_name: model_team,
                direction: 'right',
                width: '100%',
                load_model: ats_ajax.load_model,
                callback_open: add_team_open_callback,
                callback_close: edit_team_close_callback
            };
            window.panel = new AwesomePanel(args);
            panel.init();
        });

        function bind_template_change(){
            $('[name="team_template"]', ats_panel).on('change', function(e){
                var layout = model.get('layout');
                var template = $(this).val();

                model.set('styles.' + layout, templates.get(layout)[template].styles);
                model.set('styles.' + layout + '.template', template);

                preview.destroy();
                preview.init();
            });
        }

        function bind_layout_change(){
            $('[name="team_layout"]', ats_panel).on('change', function(e){
                model.set('layout', $(this).val());

                preview.destroy();
                preview.init();
            });
        }

        // Add Team open callback
        function add_team_open_callback(){
            window.preview = new AwesomeTeam();
            preview.init().done(function () {
                bind_layout_change();
                bind_template_change();
                drag.init();
            });
            $('[data-action="shortcode-team"]', ats_panel).hide();
        }

        // Edit Team open callback
        function edit_team_open_callback(){
            window.preview = new AwesomeTeam();
            preview.init().done(function () {
                bind_layout_change();
                bind_template_change();
            });
            drag.init();
            show_shortcode_button();
        }

        // Edit Team close callback
        function edit_team_close_callback(){
            preview.destroy();
            drag.destroy();
            $('select', ats_panel).dropdown('destroy');
        }

        // Call edit Team panel on team card button click
        $('[data-action="edit-team"]').on('click', function(){
            var parent = $(this).parents('li[team-id]');
            var id = parseInt($(parent).attr('team-id'));

            var args = {
                id: 'ats',
                item_id: id,
                parent: '#wpcontent',
                html: '#team-edit-wrap',
                model_name: model_team,
                direction : 'right',
                width: '100%',
                load_model: ats_ajax.load_model,
                callback_open: edit_team_open_callback,
                callback_close: edit_team_close_callback
            };
            window.panel = new AwesomePanel(args);
            panel.init();
        });
        
        function set_team_employers_callback(images) {
            var dfd = $.Deferred();
            var ids = model.get('employers_ids');
            var $employers = $('#teams-list li[team-id="' + model.get('id') + '"] .team-members');
            $.each(ids, function (index, id) {
                $employers.append('<li><div class="foto">' + images[index] + '</div>');
            });
            dfd.resolve();
            return dfd;
        }

        // update team card after team save
        function update_team_card(){
            var dfd = $.Deferred();
            var ids = model.get('employers_ids');
            var $employers = $('#teams-list li[team-id="' + model.get('id') + '"] .team-members');
            $employers.find('li[class!="empty"]').remove();
            if(ids.length > 0){
                ats_ajax.set_team_employers(ids, 'a-thumbnail', set_team_employers_callback).then(function(){
                    dfd.resolve();
                });
            }else{
                dfd.resolve();
            }
            $('#teams-list li[team-id="' + model.get('id') + '"] span.name').html(model.get('name'));
            return dfd;
        }

        // show shortcode button after create/update
        function show_shortcode_button(){
            var id = model.get('id');
            if(id !== 0){
                var result = '[a-team-showcase id="' + id + '"]';
                $('[data-action="shortcode-team"]', ats_panel).find('input').val(result);
                $('[data-action="shortcode-team"]', ats_panel).show();
            }
        }

        // Save Team
        $('#team-edit-wrap .save-panel, #team-add-wrap .save-panel').on('click', function(){
            if(window.model.locked){
                return false; // prevent double click on save button
            }

            var form = $('[data-type="form"]', ats_panel);
            var model = $(form).attr('data-model');
            var $alert = form.find('.alert');
            $alert.attr('class', 'alert').hide();
            window.model.locked = true;

            window.panel.hide_content();
            window.panel.animation_start(); // show animation

            var team_model = window.model.clone();
            team_model.unset('grid_html')
            team_model.unset('table_html')
            team_model.unset('widget_html')

            a_ajax.ajax({
                action: 'ats_update_or_new_' + model,
                data: {
                    data: team_model.toJSON(),
                },
                success_handler: function (data) {
                    window.model.set('id', data);
                    show_shortcode_button();
                    update_team_card().then(function () {
                        window.panel.show_content();
                        window.panel.animation_end();
                        window.model.locked = false;
                    });
                },
                success_error_handler: function (errors) {
                    var html = '';
                    $.each(errors, function (index, value) {
                        html += '<p>';
                        html += value.message;
                        html += '</p>';
                    });
                    $alert.addClass('alert-warning').show().html(html);
                    window.panel.show_content();
                    window.panel.animation_end();
                    window.model.locked = false;
                },
                error_handler: function (data) {
                    $alert.addClass('alert-warning').show().html(data);
                    window.model.locked = false;
                }
            });
        });

        // Delete Team on team card button click
        $('[data-action="delete-team"]').on('click', function(){
            var $parent = $(this).parents('li[team-id]');
            var $spinner = $(this).find('.a-spinner');
            var $icon = $(this).find('svg');
            var id = parseInt($parent.attr('team-id'));
            var model_name = model_team;

            if(confirm('You really want to delete team?')) {
                $icon.hide(); // hide icon
                $spinner.show(); // show animation
                a_ajax.delete_model(id, model_name).then(function () {
                    ats_animation.delete_model($parent, model_name);
                })
            }
        });

        // Get Team Shortcode
        $('[data-action="shortcode-team"]').on({'mouseenter': function(){
            var $tooltip = $(this).find('.a-tooltip-wrapper');
            setTimeout(function(){
                $tooltip.find('input').select();
            },300);
        },'mouseleave': function(){
            $(this).find('.a-tooltip-wrapper');
        }});

        // Random team style
        $('[data-action="random-team-style"]').on('click', function () {
            var colorpickers = ['base', 'card_base', 'card_border', 'card_divider', 'card_shadow', 'card_even_row', 'name', 'position', 'short_bio', 'contacts', 'social'];
            model.random();
            $.each(colorpickers, function (index, value) {
                var field = value + '_color';
                var layout = model.get('layout');
                update_colorpicker(field, model.get('styles.' + layout + '.' + field));
            });
        });

        var update_colorpicker = function (field, value) {
            if(field == 'phone_color' || field == 'email_color' || field == 'skype_color'){
                field = 'contacts_color';
            }
            if(typeof window['colorpicker_' + field] === 'object'){
                window['colorpicker_' + field].colorpicker('setValue', value);
            }
        };

        // call modal
        /*$('[data-target]').on('click', function (e) {
            e.preventDefault();
            var target = $(this).attr('data-target');
            $(target).modal();
        });*/

        $('[data-action="modal"]').lightbox({
            minWidth: 50,
            minHeight: 100
        });

        // Custom CSS Spoiler
        $('[data-toggle="collapse"]').on('click', function () {
            var target_id = $(this).attr('data-target');
            $(target_id, ats_panel).toggleClass('in');
        });

    });
})( jQuery );