/**
 * Team preview
 */

var AwesomeTeam = function (){
    'use strict';
    var $ = jQuery; // enable jQuery
    var self = this;
    var tooltip_hint = '<p class="hint">Notice: style settings for Skype, Phone, Email and Website are common for all these fields.</p>';

    self.preview = $('.team-preview', ats_panel);
    self.employers_panel = '#panel-employers';
    self.layout = model.get('layout');
    self.colorpickers = [
        'button',
        'button_hover',
        'button_text',
        'button_text_hover',
        'base',
        'panel',
        'card_base',
        'card_border',
        'divider',
        'card_shadow',
        'card_even_row',
        'name',
        'position',
        'short_bio',
        'contacts',
        'social',
        'skills_bar',
        'skills_text'
    ];
    self.tooltips = {
        photo: {
            type: 'photo',
            hint: ''
        },
        name: {
            type: 'font',
            hint: ''
        },
        position: {
            type: 'font',
            hint: ''
        },
        short_bio: {
            type: 'font',
            hint: ''
        },
        divider: {
            type: 'divider',
            hint: ''
        },
        social: {
            type: 'social',
            hint: ''
        },
        contacts: {
            type: 'font',
            hint: tooltip_hint
        },
        skills: {
            type: 'skills',
            hint: ''
        }
    };

    self.labels = {
        grid: {
            name: 'Card',
            card_divider_color: 'Divider'
        },
        table: {
            name: 'Row',
            card_divider_color: 'Separators'
        },
        widget: {
            name: 'Widget',
            card_divider_color: 'Separators'
        },
    };

    // show personal style fields
    self.show_fields = function(){
        var dfd = $.Deferred();
        var blocks = model.get('styles.' + model.get('layout') + '.blocks_order');
        if(model.get('layout') == 'grid' && $.inArray('body', blocks) == -1){
            blocks.push('body'); // add pseudo-block body for grid layout
        }
        $('.team-blocks-visibility .visible').hide();
        $.each(blocks, function (index, item) {
            $('.' + item + '_visible').show();
        });

        blocks = model.get('styles.' + model.get('layout'));

        $('#team-form .model-field').hide();
        $.each(blocks, function (key, value) {
            $('#team-form .' + key).show(); // show field block
            if(key in self.labels[model.get('layout')]){
                $('#team-form .' + key + ' .input-group-addon', ats_panel).eq(0).html(self.labels[model.get('layout')][key]); // rename layout special fields labels if exist
            }
        });

        //$('.interface-elements').show();
        $('.interface-styling').show();

        $('h5.card-label', ats_panel).html(self.labels[model.get('layout')].name + ' settings');
        $('h5.layout-label', ats_panel).show();
        if(model.get('layout') != 'grid'){
            $('h5.layout-label', ats_panel).hide();
        }
        if(model.get('layout') == 'widget'){
            //$('.interface-elements').hide();
            $('.interface-styling').hide();
        }
        $('.empty-model .hint').html('Add at least one employee from the main plugins\' screen.');
        dfd.resolve();
        return dfd;
    };

    self.insert_preview_html = function () {
        var layout = model.get('layout');
        var html = model.get(layout + '_html');
        self.preview.html(html); // append preview based on current team layout option
    };

    // two way data bindings for edit team form
    self.bind_form = function(){
        var dfd = $.Deferred();

        // layout align
        $('.team-align .btn', ats_panel).on('click', function(e){
            e.preventDefault();
            var $el = $(this);
            var $parent = $el.parent('.btn-group');
            var $input = $el.find('input');
            var value = $input.attr('data-value');
            var model_field = 'styles.' + model.get('layout') + '.align';

            $parent.find('.btn').removeClass('active');
            model.set(model_field, value);
            $el.addClass('active');
        });

        // preview blocks visibility
        $('.toggle', ats_panel).on('click', function(e){
            e.preventDefault();
            var $el = $(this);
            var $input = $el.find('input');
            var field = $input.attr('name').split('team_').join('');
            var model_field = 'styles.' + model.get('layout') + '.' + field;
            $input.prop('checked', 'checked');
            if($input.attr('data-checked') == 1){
                model.set(model_field, '0');
                $input.val('0');
                $input.attr('data-checked', '0');
            }else{
                model.set(model_field, '1');
                $input.val('1');
                $input.attr('data-checked', '1');
            }
        });

        // panels accordion
        $('.panel-collapse-team .panel-heading').on('click', function () {
            var opened = $(this).hasClass('a-collapse-head-open');
            if(opened){
                return false;
            }
            var group = $(this).parents('.panel-collapse-team').eq(0);
            $('.panel-body', group).addClass('a-collapse');
            $('.panel-heading', group).removeClass('a-collapse-head-open');
            $(this).next('.panel-body').removeClass('a-collapse');
            $(this).toggleClass('a-collapse-head-open');
        });

        window.team_form_view = new TeamForm({
            model: window.model
        });
        team_form_view.setElement($('#team-form', ats_panel));
        team_form_view.render();
        dfd.resolve();
        return dfd;
    };

    // two way data binding for team preview
    self.bind_preview = function(){
        var dfd = $.Deferred();
        window.preview_view = new TeamPreview({
            model: window.model
        });
        preview_view.setElement($('#team-form', ats_panel));
        preview_view.render();
        dfd.resolve();
        return dfd;
    };

    // init jQuery UI drag & drop for preview blocks
    self.init_sortable_fields = function(){
        var dfd = $.Deferred();
        var axis = model.get('layout') == 'table' ? 'x' : 'y';
        var card = self.preview.find('ul li .sortable-box');
        card.sortable({
            axis: axis,
            forcePlaceholderSize: true,
            forceHelperSize: true,
            placeholder: 'preview-placeholder',
            helper: function(event, ui){
                var $clone =  $(ui).clone();
                var $wrap = $('<div></div>');
                $clone.css('display', 'table-cell');
                $wrap.css('display', 'table');
                $wrap.css('vertical-align', 'middle');
                $wrap.append($clone);
                $wrap.css('margin-top', $(ui).css('margin-top'));
                $wrap.css('width', '100%');
                return $wrap;
            },
            start: function(event, ui){
                ui.placeholder.height(ui.helper.height());
                ui.placeholder.width(ui.helper.width());
                ui.placeholder.css('margin-top', ui.helper.css('margin-top'));
            },
            update: function (event, ui) {
                var blocks = card.find('.sortable');
                var order = [];
                $.each(blocks, function () {
                    var block = $(this).attr('data-block-name');
                    order.push(block);
                });
                $('[name="team_blocks_order"]', ats_panel).val(JSON.stringify(order)); // set new blocks order to hidden field
                model.set('styles.' + model.get('layout') + '.blocks_order', order);
            }
        });
        dfd.resolve();
        return dfd;
    };

    self.set_rgba_color = function (field, color) {
        var model_base = 'styles.' + model.get('layout') + '.';
        var rgba = color.toRGB();
        color = 'rgba(' + rgba.r + ',' + rgba.g + ',' + rgba.b + ',' + rgba.a + ')';
        model.set(model_base + field, color);
    };

    // init colorpickers
    self.init_colorpicker = function () {
        var dfd = $.Deferred();

        // main form colorpickers
        $.each(self.colorpickers, function (index, key) {
            var $el = $('.' + key + '_color', ats_panel);
            var colorpicker = 'colorpicker_' + key + '_color';
            window[colorpicker] = $el.colorpicker({
                container: $('.' + key + '_color .color', ats_panel),
            }).on('changeColor.colorpicker', function (e) {
                self.set_rgba_color(key + '_color', e.color);
            });
        });

        return dfd;
    };

    // two way data binding for preview tooltips
    self.bind_tooltips = function () {
        $('.team-photo-settings select', ats_panel).on('change', function () {
            var field = $(this).attr('data-field');
            var val = $(this).val();
            model.set('styles.' + model.get('layout') + '.' + field, val);
        });

        // bind some shit on photo size change
        $('.team-photo-settings [name="team_photo_size_select"]', ats_panel).on('change', function () {
            var val = $(this).val();
            var layout = model.get('layout');
            var photo = $('.employer_photo img', ats_panel);

            if(layout != 'grid'){
                return false;
            }

            $('[name="team_photo_size"]', ats_panel).val(val);
        });

        // handle btn group changes for radiobuttons-like inputs
        $('.btn-group-radio .btn', ats_panel).on('click', function(e){
            e.preventDefault();
            var $el = $(this);
            var node = $el.attr('data-model-node') || '';
            var $parent = $el.parent('.btn-group');
            var $input = $el.find('input');
            var value = $input.attr('data-value');
            var field_name = $parent.attr('data-field-name');
            if(node == 'root'){
                field_name = field_name;
            }
            if(node == 'form'){
                field_name = node + '.' + field_name;
            }
            if(node == ''){
                field_name = 'styles.' + model.get('layout') + '.' + field_name;
            }
            var option_name = $el.attr('data-option-name');

            $input.prop('checked', 'checked');
            $input.val(value);

            $parent.find('.btn').removeClass('active');
            model.set(field_name + '_' + option_name, value);
            $el.addClass('active');
        });

        // handle btn group changes for checkbox-like inputs
        $('.btn-group-checkbox .btn', ats_panel).on('click', function(e){
            e.preventDefault();
            var $el = $(this);
            var $input = $el.find('input');
            var field_name = $input.attr('name').split('team_').join('');
            field_name = 'styles.' + model.get('layout') + '.' + field_name;

            if(parseInt($input.val()) == 1){
                model.set(field_name, '0');
                $input.val('0');
                $el.removeClass('active');
            }else{
                model.set(field_name, '1');
                $input.val('1');
                $el.addClass('active');
            }
        });

        // show tooltip on A glyphicon click
        $('[data-action="call-tooltip"]', ats_panel).on('click', function (e) {
            e.preventDefault();
            var $this = $(this);
            var field = $(this).parent().attr('data-tooltip-name');
            var $toolptip = $('[data-field="' + field + '"]', ats_panel);

            $('.a-tooltip-wrapper', ats_panel).css('visibility', 'hidden').css('opacity', 0);
            $toolptip.css('visibility', 'visible').css('opacity', 1);
        });

        // hide tooltip on document click anywhere outside tooltip
        $(document).on('click', function (e) {
            if(typeof ats_panel === 'undefined'){
                return;
            }
            if( !$(e.target).parents('.a-tooltip-wrapper').length &&
                !$(e.target).is('[data-action="call-tooltip"]') &&
                !$(e.target).parents('button').is('[data-action="call-tooltip"]')
                ){
                    var $tooltip = $('.live-preview .a-tooltip-wrapper', ats_panel);
                    if($tooltip.is(':visible')){
                        $tooltip.css('visibility', 'hidden').css('opacity', 0);
                    }
                }
        });

        // Init Backbone views for tooltips
        $.each(self.tooltips, function (key, value) {
            var view_name = 'TeamTooltip' + value.type.capitalize();
            var tooltip = 'view_tooltip_' + key;
            window[tooltip] = new window[view_name]({
                model: window.model,
                el: $('.' + key + '_settings', ats_panel),
                field_name: key // set current field name for data bindings
            });
            window[tooltip].render();
        });
    };

    // init tooltips for each preview block (tooltips list stored in self.tooltips)
    self.init_tooltips = function () {
        var dfd = $.Deferred();
        $.each(self.tooltips, function (key, value) {
            var args = {
                key: key,
                width: value.width,
                viewport: '.a-slide-panel',
                direction: 'bottom',
                container: $('#team-tooltips', ats_panel),
                content: '#team-' + value.type + '-settings',
                data: {
                    field_name: key,
                    hint: value.hint
                }
            };
            window[key + '_tooltip'] = new AwesomeTooltip(args);
            window[key + '_tooltip'].init();
        });
        self.bind_tooltips();
        dfd.resolve();
        return dfd;
    };
    
    self.init_help = function () {
        var dfd = $.Deferred();
        var $clone = $('#preview-help').clone().removeAttr('id').attr('panel', 'preview-help').css({'zIndex': 4});
        $('#team-form .col-md-4').append($clone.addClass('at-transition'));
        dfd.resolve();
        return dfd;
    };

    self.destroy_colorpickers = function () {
        $.each(self.colorpickers, function (index, key) {
            var colorpicker = 'colorpicker_' + key + '_color';
            delete window[colorpicker];
        });
    };

    // destroy preview
    self.destroy = function(){
        $('.toggle').off();
        $('select', ats_panel).dropdown('destroy');
        $('#team-tooltips', ats_panel).html('');
        preview_view.close();
        team_form_view.close();
        $.each(self.tooltips, function (key, value) {
            window['view_tooltip_' + key ].close();
        });
        $.each(self.tooltips, function (key, value) {
            window[key + '_tooltip'].destroy();
        });
    };

    self.reinit = function () {
        var form = new AwesomeForm();
        form.init(model.attributes); // refill form
        self.show_fields();
        self.bind_preview();
        self.init_sortable_fields();
    };
    
    // init preview
    self.init = function(){
        var form = new AwesomeForm();

        //var def1 = self.init_help();
        var def2 = form.init(model.attributes); // refill form
        var def3 = self.show_fields();
        var def4 = self.bind_form();
        var def5 = self.bind_preview();
        var def6 = self.init_tooltips();
        var def7 = self.init_sortable_fields();

        var dfd = $.Deferred();
        $.when(def2, def3, def4, def5, def6, def7).then(function () {
            self.init_colorpicker();
            $('select', ats_panel).dropdown();
            dfd.resolve();
        });
        return dfd;
    };
};