var rules = {
    grid: {
        exclude: []
    },
    table: {
        exclude: [
            'name_color',
            'position_color',
            'short_bio_color',
            'social_color'
        ]
    },
    widget: {
        exclude: [
            'photo_size'
        ]
    },
};

var AwesomeRandom = function () {
    var self = this;
    self.number = function (limit) {
        if(limit.length > 2){
            return;
        }
        var min = limit[0];
        var max = limit[1];
        return Math.floor(Math.random() * (max - min + 1) + min);
    };

    self.boolean = function () {
        return this.number([0,1]);
    };

    self.size = function (limit) {
        return this.number(limit) + 'px';
    };

    self.percent = function (limit) {
        return this.number(limit) + '%';
    };

    self.radio = function (values) {
        if(values.length < 1){
            return;
        }
        return values[Math.floor(Math.random() * values.length)];
    };

    self.array = function (array) {
        return _.shuffle(array);
    };

    self.color_delta = function (number, range) {
        var min = parseInt(number) - range < 0 ? 0 : parseInt(number) - range;
        var max = parseInt(number) + range > 255 ? 255 : parseInt(number) + range;
        return [min, max];
    };

    self.invert_color = function (color) {
        var r = 0xFF ^ color[0];
        var g = 0xFF ^ color[1];
        var b = 0xFF ^ color[2];
        return [r,g,b];
    };
    
    self.black_or_white = function (color) {
        var lightness = color[0]*0.2126 + color[1]*0.7152 + color[2]*0.0722;
        return lightness >= 127 ? [0, 0, 0] : [255, 255, 255];
    };

    self.color = function (seed_color) {
        var limits = [];
        if(seed_color === null){
            limits[0] = [0, 255];
            limits[1] = [0, 255];
            limits[2] = [0, 255];
        }else{
            limits[0] = self.color_delta(seed_color[0], 10);
            limits[1] = self.color_delta(seed_color[1], 10);
            limits[2] = self.color_delta(seed_color[2], 10);
        }
        var r = Math.floor(this.number(limits[0]));
        var g = Math.floor(this.number(limits[1]));
        var b = Math.floor(this.number(limits[2]));
        //var a = parseFloat(Math.random()).toFixed(2);
        return [r,g,b];
    };

};

var AwesomeModel = Backbone.NestedModel.extend({
    push: function (arg, val) {
        if(typeof this.get(arg) == 'undefined'){
            this.set(arg, []);
        }
        var arr = _.clone(this.get(arg));
        arr.push(val);
        this.set(arg, arr);
    },

    unset_array: function (arg, index) {
        var arr = _.clone(this.get(arg));
        arr.splice(index, 1);
        this.set(arg, arr);
    },

    make_seed: function (args) {
        var random = new AwesomeRandom();
        var self = this;
        var rules = args.rules;
        var seed = args.seed;
        var fields = args.fields;
        var seed_card_color = args.seed_card_color;

        if(seed_card_color === null) {
            seed_card_color = random.color(seed_card_color);
        }

        $.each(fields, function (key, value) {
            if(value.type == 'group'){
                var fields = {};
                $.each(value.members, function (index, inner_value) {
                    fields[inner_value] = value.field;

                });
                seed = self.make_seed({
                    fields: fields,
                    seed: seed,
                    seed_card_color: seed_card_color,
                    //seed_text_color: seed_text_color
                });
            }else{
                var field_value  = null;
                switch (value.type){
                    case 'number':
                        field_value = random.number(value.limits);
                        break;
                    case 'boolean':
                        field_value = random.boolean();
                        break;
                    case 'size':
                        field_value = random.size(value.limits);
                        break;
                    case 'percent':
                        field_value = random.percent(value.limits);
                        break;
                    case 'radio':
                        field_value = random.radio(value.values);
                        break;
                    case 'array':
                        field_value = random.array(value.values);
                        break;
                    case 'color':
                        var alpha = null;
                        if(value.group == 'card'){
                            alpha = '0.' + random.number([0,99]);
                            var color = random.color(seed_card_color);
                            field_value = 'rgba(' + color.join(',') + ',' +  alpha + ')';
                        }
                        if(value.group == 'text'){
                            alpha = '0.' + random.number([50,99]);
                            if(seed_card_color === null) {
                                return;
                            }
                            var inverted = random.black_or_white(seed_card_color);
                            field_value = 'rgba(' + inverted.join(',') + ',' +  alpha + ')';
                        }
                        break;
                }
                seed[key] = field_value;
            }
        });
        return seed;
    },


    random: function () {
        var random_values = {};
        var args = {
            fields: fields,
            seed: random_values,
            seed_card_color: null,
            //seed_text_color: null
        };
        var seed = this.make_seed(args);

        var layout = model.get('layout');
        var styles = model.get('styles.' + layout);

        $.each(styles, function (key, value) {
            if($.inArray(key, rules[layout].exclude) > -1){
                return;
            }
            if(seed.hasOwnProperty(key)){
                model.set('styles.' + layout + '.' + key, seed[key]);
            }
        });
    }
});