(function ($) {
// Backbone View for edit team form
    EmployerForm = Backbone.View.extend({
        defaults: {
            skill: {
                star_options: [
                    {
                        value: '1',
                        label: 'One'
                    },
                    {
                        value: '2',
                        label: 'Two',
                    },
                    {
                        value: '3',
                        label: 'Three',
                    },
                    {
                        value: '4',
                        label: 'Four',
                    },
                    {
                        value: '5',
                        label: 'Five',
                    }
                ]
            }
        },
        template: {
            skill: {
                add: {
                    body: _.template('\
                    <div class="input-group form-group">\
                        <div class="display-table">\
                            <div class="display-table-cell name">\
                                <div class="input-group form-group">\
                                    <span class="input-group-addon">Skill:</span>\
                                    <input type="text" class="form-control" name="employer_skill_name" placeholder="e.g. Photoshop" value="<%= name %>">\
                                </div>\
                            </div>\
                            <div class="display-table-cell value">\
                                <%= value %>\
                            </div>\
                        </div>\
                        <div data-type="skill-alert" class="skill-alert"></div>\
                        <div class="buttons">\
                            <%= buttons %>\
                        </div>\
                    </div>\
                    '),
                    type: _.template('\
                    <div class="btn-group filter_text_transform btn-group-text-transform btn-group-radio model-field" data-field-name="skill_type">\
                        <label type="button" class="btn btn-default <% if(percent_active){ %> active <% } %> skill_type" data-option-name="skill_type">\
                            <input type="radio" style="display: none;" name="employer_skill_type" checked data-value="percent" />\
                            <svg class="icon icon-percent" viewBox="0 0 320 320">\
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-percent"></use>\
                            </svg>\
                        </label>\
                        <label type="button" class="btn btn-default <% if(star_active){ %> active <% } %> skill_type" data-option-name="skill_type">\
                            <input type="radio" style="display: none;" name="employer_skill_type" data-value="star"/>\
                            <svg class="icon icon-star" viewBox="0 0 1024 1024">\
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-star-full"></use>\
                            </svg>\
                        </label>\
                    </div>'),
                    value: _.template('\
                        <div class="input-group model-field skill-value-percent">\
                            <span class="input-group-addon">% of 100:</span>\
                            <input type="text" class="form-control" name="employer_skill_value" placeholder="" value="<%= value %>"/>\
                        </div>'),
                    buttons: {
                        add: _.template('\
                        <button class="btn btn-default" type="button" data-action="add-skill">\
                            Add Skill\
                        </button>'),
                        edit: _.template('\
                        <button class="btn btn-sm btn-default" type="button" data-action="save-skill">\
                            <div class="a-slide-panel-spinner a-spinner" data-wr_replaced="true"  style="display: none;">\
                                <div class="bounce1"></div>\
                                <div class="bounce2"></div>\
                                <div class="bounce3"></div>\
                            </div>\
                            <span class="text">Save Skill</span>\
                        </button>\
                        <button class="btn btn-sm btn-default" type="button" data-action="cancel-skill-edit">\
                            Cancel\
                        </button>')
                    }

                },
                body: _.template('\
                <li id="skill-<%= id %>" class="skill">\
                    <div class="skill-box">\
                    <%= value %>\
                    <%= name %>\
                        <span class="actions">\
                            <button class="svg" data-action="edit-skill" title="Edit skill">\
                                <svg class="icon" viewBox="0 0 28 28">\
                                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-pen"></use>\
                                </svg>\
                            </button>\
                            <button class="svg" data-action="delete-skill" title="Delete skill">\
                                <svg class="icon" viewBox="0 0 28 28">\
                                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-trash"></use>\
                                </svg>\
                            </button>\
                        </span>\
                    </div>\
                </li>'),
                value: _.template('<span class="value"><%= value %></span>'),
                star: '<svg class="icon icon-star" viewBox="0 0 1024 1024">\
                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-star-full"></use>\
                </svg>',
                name: _.template('<span class="name"><%= name %></span>')
            }
        },

        initialize: function () {
            var value = this.template.skill.add.value({
                value: 0
            });
            var buttons = this.template.skill.add.buttons.add();
            var template = this.template.skill.add.body({
                name: '',
                value: value,
                buttons: buttons
            });
            $('.skill-form', ats_panel).html(template);
            $('select[name="employer_skill_value"]', ats_panel).dropdown();
        },

        edit_skill: function (index) {
            var skills = model.get('custom_fields.skills');
            var skill = skills[index];

            var value = this.template.skill.add.value({
                value: skill.type == 'percent' ? skill.value : ''
            });
            var buttons = this.template.skill.add.buttons.edit();
            var template = this.template.skill.add.body({
                name: skill.name,
                value: value,
                buttons: buttons
            });
            var template = $('<div class="skill-form skill-inline-form" data-form="edit-skill">' + template + '</div>');

            // hide all other forms
            $('.skills-list .skill-inline-form', ats_panel).remove();
            $('.skills-list li.skill .skill-box').show();

            $('.skills-list li#skill-' + index + ' .skill-box').hide();

            var select_value = skill.type == 'star' ? skill.value : 1;

            $('.skills-list li#skill-' + index, ats_panel).prepend(template);
            $('select[name="employer_skill_value"]', ats_panel).val(select_value);
            $('select[name="employer_skill_value"]', ats_panel).dropdown();
        },

        bindings: function () { // bindings for input fields
            var result = {};
            var cf = 'custom_fields.';

            result['[name="employer_name"]']            = 'name';
            result['[name="employer_department"]']      = 'department';
            result['[name="employer_short_bio"]']       = 'short_bio';

            result['[name="employer_position"]']        = cf + 'position';
            result['[name="employer_thumbnail_id"]']    = cf + 'thumbnail_id';
            result['[name="employer_panel_text"]']      = cf + 'panel_text';

            result['[name="employer_email"]']           = cf + 'email';
            result['[name="employer_phone"]']           = cf + 'phone';
            result['[name="employer_skype"]']           = cf + 'skype';
            result['[name="employer_link"]']            = cf + 'link';
            result['[name="employer_link_text"]']       = cf + 'link_text';
            result['[name="employer_location"]']        = cf + 'location';
            result['[name="employer_profile"]']         = cf + 'profile';
            result['[name="employer_facebook"]']        = cf + 'facebook';
            result['[name="employer_twitter"]']         = cf + 'twitter';
            result['[name="employer_linkedin"]']        = cf + 'linkedin';
            result['[name="employer_google"]']          = cf + 'google';
            result['[name="employer_instagram"]']       = cf + 'instagram';
            result['[name="employer_pinterest"]']       = cf + 'pinterest';
            result['[name="employer_youtube"]']         = cf + 'youtube';
            result['[name="employer_vimeo"]']           = cf + 'vimeo';
            result['[name="employer_dribble"]']         = cf + 'dribble';
            result['[name="employer_flickr"]']          = cf + 'flickr';
            result['[name="employer_github"]']          = cf + 'github';

            result['.skills-list'] = {
                observe: 'custom_fields.skills',
                update: function ($el, val, model, options) {
                    var self = this;
                    var order = model.get('custom_fields.skills_order');

                    var skills_ordered = [];
                    _.each(order, function (item, index) {
                        skills_ordered[index] = val[item];
                    })

                    $el.html('');

                    if (val.length === 0) {
                        return;
                    }
                    $.each(skills_ordered, function (index, item) {
                        var name = self.template.skill.name({name: item.name});
                        var value = item.value || 0;
                        value = value + '%';
                        if (item.type == 'star') {
                            value = '';
                            for (var i = 0; i < parseInt(item.value); i++) {
                                value += self.template.skill.star;
                            }
                        }
                        var value = self.template.skill.value({value: value});
                        var skill = {
                            id: order[index],
                            value: value,
                            name: name
                        };
                        var body = self.template.skill.body(skill);
                        $el.append(body);
                    });

                    var employer_view = new AwesomeEmployer();
                    employer_view.update_skill_order();
                }
            };

            return result;
        },
        render: function () {
            this.stickit();
            return this;
        }
    });
})(jQuery);
