/**
 * Ajax functions, used in Panel
 */

var ATS_Ajax = function(){
    'use strict';

    var $ = jQuery; // enable jQuery
    var prefix = 'ats';
    var a_ajax = new AwesomeAjax(prefix);
    var self = this;

    self.result = {};

    // load model to panel
    self.load_model = function(item_id, model_name){
        if($.isNumeric(item_id)){
            return a_ajax.load_model(
                model_name,
                item_id,
                function (data) {
                    window.model = new AwesomeModel(data); // set window model to Backbone model
                }
            );
        }
        if(item_id == '#'){
            if(model_name == 'team'){
                return a_ajax.ajax({
                    action: prefix + '_get_team_defaults',
                    success_handler: function (data) {
                        window.model = new AwesomeModel(data); // set window model to Backbone model
                    }
                });
            }
            if(model_name == 'employer'){
                return a_ajax.ajax({
                    action: prefix + '_get_employer_defaults',
                    success_handler: function (data) {
                        window.model = new AwesomeModel(data); // set window model to Backbone model
                    }
                });
            }
        }
    }

    // Fill team layout html
    self.fill_team_layout_html = function (container, key, value) {
        //container.find('#team-preview').html(value);
    };

    self.fill_blocks_order = function (container, key, value) {
        var order = JSON.stringify(value);
        container.find('[name="team_' + key + '"]').val(order);
    };

    self.fill_team_grid_blocks_order = self.fill_blocks_order.bind(self);
    self.fill_team_table_blocks_order = self.fill_blocks_order.bind(self);

    // Fill team employers list (employer round avatars)
    self.fill_team_employers = function(container, key, employers){
        var html = '';
        var ids = [];
        $.each(employers, function(index, item){
            ids.push(item.ID);
            var foto = item.foto ? '<img width="40" height="40" src="' + item.foto + '">' : '<img src="' + window[prefix + '_localize']['default_avatar'] + '" alt="" />';

            /* jshint multistr: true */
            var template = '\
                <li data-action="edit-employer" class="clearfix" employer-id="{ID}" title="{name}">\
                    <div class="foto" data-wr_replaced="true">\
                        {foto}\
                    </div>\
                    <span class="name">{name}</span>\
                    <span class="position">{position}</span>\
                    <div class="teams"><span class="teams_count">{teams_count}</span> Teams</div>\
                    <button class="btn-action remove-employer at-transition at-transition-fast" title="Remove" data-action="remove-employer">\
                        <svg viewBox="0 0 20 20">\
                            <use xlink:href="#icon-delete"></use>\
                        </svg>\
                    </button>\
                </li>';
            html += template.replace_all('{name}', _.escape(item.post_title))
                .replace_all('{foto}', foto)
                .replace_all('{ID}', item.ID)
                .replace_all('{position}', _.escape(item.position))
                .replace_all('{teams_count}', item.teams_count);
        });

        $(container).find('#team-employers').val(JSON.stringify(ids));
        $(container).find('#team-sortable').html('').append(html);
    };

    // fill colorpicker
    self.fill_color = function (container, key, value) {
        key = key.replace_all('team_', '');
        $(container).find('.' + key).find('input').val(value);
        $(container).find('.' + key).find('span.color > i').css('background-color', value);
    };

    // fill colorpickers
    self.fill_team_button_color = self.fill_color.bind(self);
    self.fill_team_button_hover_color = self.fill_color.bind(self);
    self.fill_team_button_text_color = self.fill_color.bind(self);
    self.fill_team_button_text_hover_color = self.fill_color.bind(self);
    self.fill_team_base_color = self.fill_color.bind(self);
    self.fill_team_card_base_color = self.fill_color.bind(self);
    self.fill_team_card_border_color = self.fill_color.bind(self);
    self.fill_team_card_divider_color = self.fill_color.bind(self);
    self.fill_team_card_shadow_color = self.fill_color.bind(self);
    self.fill_team_card_even_row_color = self.fill_color.bind(self);
    self.fill_team_panel_color = self.fill_color.bind(self);

    // fill team avatar shape select (formstone)
    self.fill_team_photo_shape = function(container, key, value){
        //$(container).find('[name="team_photo_shape"]').val(value).trigger('change');
    };

    // fill team avatar size select (formstone)
    self.fill_team_photo_size = function(container, key, value){
        //$(container).find('[name="team_photo_size"]').val(value).trigger('change');
    };

    // fill blocks visibility
    self.fill_visibility = function(container, key, value){
        $(container).find('[name="' + key + '"]').attr('data-checked', value).val(value);
    };

    // fill team block visibility
    self.fill_team_slider = self.fill_visibility.bind(self);
    self.fill_team_slider_autoplay = self.fill_visibility.bind(self);
    self.fill_team_slider_adaptive = self.fill_visibility.bind(self);
    self.fill_team_filter = self.fill_visibility.bind(self);
    self.fill_team_panel = self.fill_visibility.bind(self);
    self.fill_team_panel_contacts_visible = self.fill_visibility.bind(self);
    self.fill_team_panel_social_visible = self.fill_visibility.bind(self);
    self.fill_team_reveal = self.fill_visibility.bind(self);
    self.fill_team_body_visible = self.fill_visibility.bind(self);
    self.fill_team_photo_visible = self.fill_visibility.bind(self);
    self.fill_team_name_visible = self.fill_visibility.bind(self);
    self.fill_team_divider_visible = self.fill_visibility.bind(self);
    self.fill_team_position_visible = self.fill_visibility.bind(self);
    self.fill_team_short_bio_visible = self.fill_visibility.bind(self);
    self.fill_team_phone_visible = self.fill_visibility.bind(self);
    self.fill_team_email_visible = self.fill_visibility.bind(self);
    self.fill_team_skype_visible = self.fill_visibility.bind(self);
    self.fill_team_link_visible = self.fill_visibility.bind(self);
    self.fill_team_location_visible = self.fill_visibility.bind(self);
    self.fill_team_social_visible = self.fill_visibility.bind(self);
    self.fill_team_skills_visible = self.fill_visibility.bind(self);

    // fill employer photo
    self.fill_employer_foto = function(container, key, value){
        $('#employer-form-foto-remove', ats_panel).show();
        if(value === ''){
            $('#employer-form-foto-remove', ats_panel).hide();
        }
        var img = $('<img />').attr('src', value);
        $(container).find('#employer-form-foto').html('').append(img);
    };

    self.fill_employer_panel_text = function(container, key, value) {
        window.employer_panel_text_dfd = $.Deferred();
        a_ajax.ajax({
            action: prefix + '_get_tiny_mce',
            data: {
                id: key,
                content: value
            },
            success_handler: function (result) {
                $('.' + key, container).html(result);
                if(tinyMCE.get(key)){
                    tinymce.editors = [];
                }
                tinyMCEPreInit.mceInit[key].setup = function(editor) {
                    editor.on('init', function(e) {
                        employer_panel_text_dfd.resolve('tinymce init');
                    });
                    editor.on('change', function (e) {
                        model.set('custom_fields.panel_text', editor.getContent())
                    });
                    editor.on('PostRender')
                }
                tinyMCE.init(tinyMCEPreInit.mceInit[key]);
                quicktags( tinyMCEPreInit.qtInit[key] );
            }
        });
    }

    // fill team blocks preview checkboxes
    self.fill_checkbox = function (container, key, value) {
        if(key == 'team_name_bold'){
        }
        $(container).find('[name="' + key + '"]').attr('data-checked', value).val(value);
    };

    // fill team preview checkboxes (is_bold)
    self.fill_team_filter_bold = self.fill_checkbox.bind(self);
    self.fill_team_name_bold = self.fill_checkbox.bind(self);
    self.fill_team_position_bold = self.fill_checkbox.bind(self);
    self.fill_team_short_bio_bold = self.fill_checkbox.bind(self);
    self.fill_team_contacts_bold = self.fill_checkbox.bind(self);

    // fill team preview checkboxes (is_italic)
    self.fill_team_filter_italic = self.fill_checkbox.bind(self);
    self.fill_team_name_italic = self.fill_checkbox.bind(self);
    self.fill_team_position_italic = self.fill_checkbox.bind(self);
    self.fill_team_short_bio_italic = self.fill_checkbox.bind(self);
    self.fill_team_contacts_italic = self.fill_checkbox.bind(self);


    self.fill_select = function(container, key, value){
        $(container).find('[name="' + key + '"]').val(value);
        $(container).find('[name="' + key + '"]').dropdown('update');
    };

    // fill team layout select (formstone)
    self.fill_team_layout = self.fill_select.bind(self);
    self.fill_team_order_by = self.fill_select.bind(self);
    self.fill_team_hover = self.fill_select.bind(self);
    self.fill_team_panel_theme = self.fill_select.bind(self);

    // fill team template
    self.fill_team_template = function (container, key, value) {
        var html = '';
        var layout = window.model.get('layout');
        $.each(window.templates.get(layout), function(index, template){
            html += '<option value="' + index + '">' + template.label + '</option>';
        });
        $(container).find('[name="' + key + '"]').html('').html(html).val(value);
        $(container).find('[name="' + key + '"]').dropdown('update');
    };

    self.set_employer_photo = function (id, size, callback) {
        return a_ajax.ajax({
            action: prefix + '_get_team_preview_photo',
            data: {
                'id': id,
                'size': size,
            },
            success_handler: function (result) {
                var img = result !== '' ? result : '<img src="' + window[prefix + '_localize']['default_avatar'] + '"/>';
                callback(img);
            }
        });
    }

    self.set_team_employers = function (ids, size, callback) {
        return a_ajax.ajax({
            action: prefix + '_get_team_employers_photos',
            data: {
                'ids': ids,
                'size': size,
            },
            success_handler: function (result) {
                callback(result);
            }
        });
    };

    // get templates
    self.get_templates = function () {
        return a_ajax.ajax({
            action: prefix + '_get_templates',
            success_handler: function (result) {
                window.templates = new Backbone.NestedModel(result);
            }
        });
    }

    // init employer departmets
    self.init_departments = function () {
        return a_ajax.ajax({
            action: prefix + '_get_taxonomy_terms',
            data: {
                taxonomy: prefix + '-employer-taxonomy'
            },
            success_handler: function (result) {
                window.ats_employers_taxonomy_terms = result;
            }
        });
    }

    self.get_employer_form = function (callback) {
        return a_ajax.ajax({
            action: prefix + '_get_employer_form',
            data: {

            },
            success_handler: function (result) {
                callback(result);
            }
        });
    }
};