/**
 * jQuery UI Drag & Drop
 */

var AwesomeDrag = function () {
    'use strict';

    var $ = jQuery; // enable jQuery
    var self = this;
    self.sortableEl = '.connected-sortable';
    self.employers_panel = '#panel-employers';

    // Pop up employers list for Edit Team panel
    self.cloneEmployers = function () {
        // add class for z-index
        var $clone = $(self.employers_panel).clone().removeAttr('id').attr('panel', 'employers-clone').addClass('at-transition');
        window.ats_employers_clone = ats_employers.clone();

        setTimeout(function () {
            $(self.employers_panel).fadeOut();
        }, 100);

        var all_employers = $clone.find('li');
        var model_employers = model.get('employers') || [];

        // Hide employers that already exist in team
        $.each(all_employers, function () {
            var id = parseInt($(this).attr('employer-id'));
            if (AwesomeUtil.find_in_array_of_objects(model_employers, 'ID', id)) {
                var model = ats_employers_clone.findWhere({id: id});
                $(this, $clone).remove();
                ats_employers_clone.remove(model);
            }
        });

        var $push_all_btn = $('<li><button data-action="push-all-employers"><span title="Push all">Push all</span></button></li>');
        $clone.find('.panel-heading .panel-actions').prepend($push_all_btn);

        $('.employers-list', ats_panel).html($clone);
        self.bind_clone();

        $(document).on('click', '[data-action="remove-employer"]', function(){
            var $item = $(this).parents('li[employer-id]'),
                id = +$item.attr('employer-id'),
                model = ats_employers.findWhere({id:id});
            $('#employers-list', ats_panel).append($item);
            ats_employers_clone.add(model);
            self.updateElements();
        });
    };

    // Bind highlight for drop zone on employer hover
    self.bind_clone = function () {
        $(document).on('mouseenter', ".panel-pop-up #employers-list", function () {
            $('#team-sortable', ats_panel).addClass('pulsate');
        });
        $(document).on('mouseleave', ".panel-pop-up #employers-list", function () {
            $('#team-sortable', ats_panel).removeClass('pulsate');
        });
        $(document).on('click', '[data-action="push-all-employers"]', function () {
            ats_employers_clone.reset();
            var employers = $('ul.employers-list > li', ats_panel);
            var employers_ids = _.clone(model.get('employers_ids'));
            $.each(employers, function (index, item) {
                $('#team-sortable', ats_panel).append(item);
                var id = parseInt($(item).attr('employer-id'));
                employers_ids.push(id);
            });
            model.set('employers_ids', employers_ids);
        });
        $(document).on('click', '[data-action="remove-all-employers"]', function () {
            var employers = $('#team-sortable > li', ats_panel);
            $.each(employers, function (index, item) {
                $('ul.employers-list', ats_panel).append(item);
                var id = parseInt($(item).attr('employer-id'));
                var model = ats_employers.findWhere({id: id});
                ats_employers_clone.add(model);
            });
            model.set('employers_ids', []);
        });
    }

    // Init employers drag & drop between two panels
    self.initSortable = function () {
        $(self.sortableEl).addClass('sortable-placeholder');
        $('#team-sortable, #employers-list').sortable({
            appendTo: 'body',
            zIndex: 99999,
            connectWith: self.sortableEl,
            forcePlaceholderSize: true,
            placeholder: 'sortable-placeholder-inner',
            cursor: 'move',
            cursorAt: {top: 25, left: 25},
            helper: function (event, ui) { // clone employer photo to drag
                var img = $(ui).find('.foto').clone();
                var $clone = $('<li class="item"></li>').append(img);
                return $clone;
            },
            create: function (event, ui) {
            },
            start: function (event, ui) {
                $(ui.helper).addClass('pop-up'); // add border to helper element
                $('.a-slide-panel #team-sortable').addClass('drop-highlight'); // highlight drop zone
            },
            stop: function (event, ui) {
                $('.a-slide-panel #team-sortable').removeClass('drop-highlight'); // stop drop zone highlighting
            },
            update: function (event, ui) {
                $(ui.item).attr('style', ''); // remove pop-up class on drop event to remove border
                var parent = $(ui.item[0].parentElement).attr('id');
                var id = parseInt($(ui.item[0]).attr('employer-id'));
                var model = ats_employers.findWhere({id:id});

                if(parent == 'team-sortable'){
                    ats_employers_clone.remove(model);
                }else if(parent == 'employers-list'){
                    ats_employers_clone.add(model);
                }
                self.updateElements();
            }
        });
    };

    // on every drag end save employers order and IDs to hidden field
    self.updateElements = function () {
        var $employers_field = $('#team-employers', ats_panel);
        $employers_field.val('');
        var employers = [];
        var employers_ids = [];
        $('#team-sortable li', ats_panel).each(function () {
            var id = parseInt($(this).attr('employer-id'));
            var foto = $(this).find('img').attr('src');
            var name = $(this).find('.name').html();

            var position = $(this).find('.position').html();
            var teams_count = $(this).find('.teams_count').html();
            employers_ids.push(id);
            employers.push({
                ID: id,
                foto: foto,
                post_title: name,
                position: position,
                teams_count: teams_count
            });
        });

        var result = JSON.stringify(employers_ids);
        $employers_field.val(result);
        model.set('employers_ids', employers_ids);
        model.set('employers', employers);
    };

    // destory drag & drop
    self.destroy = function () {
        $(self.employers_panel).show();
        $('.panel.panel-pop-up').remove();
        $('.connected-sortable').removeClass('sortable-placeholder');
        $('#employers-list').sortable({
            disabled: true
        });
        $('.employers-list', ats_panel).html('');
        $('.panel-pop-up #employers-list').off();
    };

    // init drag & drop
    self.init = function () {
        self.cloneEmployers(); // clone employers panel and hide employers that already exists in team
        self.updateElements();
        self.initSortable();
    };
};